@use '../abstracts/' as a;
@use 'sass:map';
@use 'sass:color';


header{
  @include a.margin-auto;
  padding: 0.5rem;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  background-color: var(--clr-header);
  position: sticky;
  top: 0;
  z-index: 10000;
  backdrop-filter: blur(8px);
}
.logo{
  @include a.display(flex, left, center, 0.5rem);
}

.features{
  display: none;
  margin-left: 2rem;
}

nav{
  grid-column: -1;
  @include a.display(flex, right, center, 2rem);
}

label[for="dark-toggle"]{
  font-weight: map-get(a.$Font-weight, bold);
  display: none;
}

.btn-s{
  display: none;
}

@media(width > 768px){
  header{
    max-width: 720px;
  }
  label[for="dark-toggle"]{
    display: inline;
  }
}

@media(width > 1200px){
  header{
    max-width: 1140px;
  }
  .hamburger{
    display: none;
  }
  .features{
    display: block;
  }
  .btn-s{
    display: block;
  }
  label[for="dark-toggle"]{
    display: inline;
  }
}